.landing{
  display: flex;
  justify-content: center;
  cursor: default;
  height: 100vh;
  min-height: 35em;
  overflow: hidden;
  text-align: center;
  margin: auto 0;
}

.landingHeader{
  font-size: 1.75em;
  text-transform: uppercase;
  letter-spacing: .225em;
  font-weight: 800;
}

.LandingContent{
  font-size: 1em;
  text-transform: uppercase;
  font-weight: 200;
  margin-top: 12px;
}

.LearnMore{
height: 200px;
width: 200px;
color: inherit;
text-decoration: none;

}
